
import { defineComponent, ref, unref } from 'vue'
import {
  IonButton,
  IonListHeader,
  IonLabel,
  IonList,
  IonItem,
  IonSelect,
  IonSelectOption,
  IonIcon,
  IonBadge,
  onIonViewWillEnter,
} from '@ionic/vue'
import { supabase } from '@/api'
import PageLayout from '@/components/PageLayout.vue'
import { useRoute } from 'vue-router'
import { pawOutline, checkmarkCircle, closeCircle } from 'ionicons/icons'
import { notify } from '@/notify'

export default defineComponent({
  name: 'CourseCardDetail',
  components: {
    IonButton,
    IonLabel,
    IonList,
    IonItem,
    IonSelect,
    IonSelectOption,
    IonIcon,
    PageLayout,
    IonBadge,
    IonListHeader,
  },
  setup() {
    const card = ref()
    const customer = ref()
    const course = ref()
    const route = useRoute()
    const { cardId } = route.params

    async function getCard() {
      const result = await supabase
        .from('cards')
        .select(
          `
          id,
          payed,
          count,
          courses (
            id,
            name
          ),
          course_dates (
            id,
            date,
            topics (
              id,
              name
            )
          ),
          customers (
            id,
            name,
            dogname
          )
        `
        )
        .match({
          id: cardId,
        })
      if (result.error) {
        notify.error('Fehler beim Laden der Karten.', result.error)
        return
      }
      card.value = result.body[0]
      course.value = unref(card).courses
      customer.value = unref(card).customers
    }

    function init() {
      getCard()
    }
    init()
    onIonViewWillEnter(init)

    async function togglePayed(card: any) {
      const { error } = await supabase
        .from('cards')
        .update({
          payed: !card.payed,
        })
        .match({ id: card.id })
      if (error) {
        notify.error('Fehler beim Ändern des Bezahlstatus.', error)
        return
      }
      getCard()
    }

    async function updateCount(event: any) {
      const { error } = await supabase
        .from('cards')
        .update({
          count: event.detail.value,
        })
        .match({ id: unref(card).id })
      if (error) {
        notify.error('Fehler beim Ändern der Kartengröße.', error)
        return
      }
      getCard()
    }

    return {
      card,
      course,
      customer,
      pawOutline,
      checkmarkCircle,
      closeCircle,
      togglePayed,
      updateCount,
    }
  },
})
